import { makeKernelMap } from '../utilities/kernel';
import { zeros2D } from '../utilities/zeros-2d';
import { BasePraxis } from './base-praxis';
import { ILayer } from '../layer/base-layer';
import {
  IKernelFunctionThis,
  IKernelMapRunShortcut,
  ISubKernelsResults,
  KernelOutput,
} from 'gpu.js';

function updateChange(value: number): number {
  return value;
}

export interface IUpdateFunctionConstants {
  learningRate: number;
  momentum: number;
}

function update(
  this: IKernelFunctionThis<IUpdateFunctionConstants>,
  changes: number[][],
  weights: number[][],
  incomingWeights: number[][],
  inputDeltas: number[][]
): number {
  const lastChange: number = changes[this.thread.y][this.thread.x];
  const inputDelta: number = inputDeltas[this.thread.y][0];
  const weight: number = weights[this.thread.y][this.thread.x];
  const incoming: number = incomingWeights[this.thread.x][0];

  const change =
    this.constants.learningRate * inputDelta * incoming +
    this.constants.momentum * lastChange;
  updateChange(change);
  return weight + change;
}

export interface IArthurDeviationWeightsSettings {
  learningRate: number;
  momentum: number;
  weightsLayer: ILayer;
  incomingLayer: ILayer;
  deltaLayer: ILayer;
}

export interface IUpdateKernelResults extends ISubKernelsResults {
  changes: KernelOutput;
}

export class ArthurDeviationWeights<
  PraxisSettingsType extends IArthurDeviationWeightsSettings = IArthurDeviationWeightsSettings
> extends BasePraxis<PraxisSettingsType> {
  static get defaults() {
    return {
      learningRate: 0.3,
      momentum: 0.1,
    };
  }

  weightsLayer: ILayer | null = null;
  incomingLayer: ILayer | null = null;
  deltaLayer: ILayer | null = null;
  changes: KernelOutput;
  kernelMap: IKernelMapRunShortcut<IUpdateKernelResults> | null = null;

  get learningRate(): number {
    return this.settings.learningRate;
  }

  get momentum(): number {
    return this.settings.momentum;
  }

  constructor(layer: ILayer, settings: PraxisSettingsType) {
    super(layer, settings);
    if (settings) {
      if (settings.weightsLayer) {
        this.weightsLayer = settings.weightsLayer;
      }
      if (settings.incomingLayer) {
        this.incomingLayer = settings.incomingLayer;
      }
      if (settings.deltaLayer) {
        this.deltaLayer = settings.deltaLayer;
      }
    }
    this.changes = zeros2D(layer.width, layer.height);
  }

  run(): KernelOutput {
    const output = (this.kernelMap as IKernelMapRunShortcut<
      IUpdateKernelResults
    >)(
      this.changes,
      (this.weightsLayer as ILayer).weights,
      (this.incomingLayer as ILayer).weights,
      (this.deltaLayer as ILayer).deltas
    );
    this.changes = output.changes;
    return output.result;
  }

  setupKernels(): void {
    this.kernelMap = makeKernelMap<
      IUpdateKernelResults,
      typeof update,
      IUpdateFunctionConstants
    >(
      {
        changes: updateChange,
      },
      update,
      {
        output: [this.width, this.height],
        constants: {
          learningRate: this.learningRate,
          momentum: this.momentum,
        },
      }
    );
  }
}

export function arthurDeviationWeights(
  layer: ILayer,
  settings: IArthurDeviationWeightsSettings
): ArthurDeviationWeights {
  return new ArthurDeviationWeights(layer, settings);
}
